function AddPickups(props) {

  //const [columns, setColumns] = useState([]);
  const [data, setData] = useState([]);
  const [isEmpty, setIsEmpty] = useState(false);

  // process CSV data
  const processData = dataString => {
    const dataStringLines = dataString.split(/\r\n|\n/);
    const headers = dataStringLines[0].split(/,(?![^"]*"(?:(?:[^"]*"){2})*[^"]*$)/);

    const list = [];
    for (let i = 1; i < dataStringLines.length; i++) {
      const row = dataStringLines[i].split(/,(?![^"]*"(?:(?:[^"]*"){2})*[^"]*$)/);
      if (headers && row.length === headers.length) {
        const obj = {};
        for (let j = 0; j < headers.length; j++) {
          let d = row[j];
          if (d.length > 0) {
            if (d[0] === '"')
              d = d.substring(1, d.length - 1);
            if (d[d.length - 1] === '"')
              d = d.substring(d.length - 2, 1);
          }
          if (headers[j]) {
            obj[headers[j]] = d;
          }
        }

        // remove the blank rows
        if (Object.values(obj).filter(x => x).length > 0) {
          list.push(obj);
        }
      }
    }

    // prepare columns list from headers
    // const columns = headers.map(c => ({
    //   name: c,
    //   selector: c,
    // }));

    setData(list);
  }



  useEffect(() => {
    if (Object.values(data).length > 0) {
      let b = data.reduce((acc, obj) => [...acc, Object.values(obj).map(y => y)], []);
      /* console.log(b) */
      const emptyData = b.filter(function (array) {
        return array.includes('')
      })

     /*  console.log(emptyData) */
      if (typeof emptyData !== 'undefined' && emptyData.length === 0) {
        setIsEmpty(true)
      }
    }

  }, [data])


  const handleTableChange = (
    type,
    { data, cellEdit: { rowId, dataField, newValue } }
  ) => {
    setTimeout(() => {
      console.log("handleTableChange")
      const result = data.map(row => {
        if (row.num === rowId) {
          const newRow = { ...row };
          newRow[dataField] = newValue;
          return newRow;
        }
        return row;
      });
      setData(result)
    }, 100);
  };


  // handle file upload
  const handleFileUpload = e => {
    setIsEmpty(false)
    console.log("handleFileUpload")
    // setData()
    const file = e.target.files[0];
    const reader = new FileReader();
    reader.onload = (evt) => {
      /* Parse data */
      const bstr = evt.target.result;
      const wb = XLSX.read(bstr, { type: 'binary' });
      /* Get first worksheet */
      const wsname = wb.SheetNames[0];
      const ws = wb.Sheets[wsname];
      /* Convert array of arrays */
      const data = XLSX.utils.sheet_to_csv(ws, { header: 1 });
      processData(data);
    };
    reader.readAsBinaryString(file);
  }
  const senddata = e => {
    console.log(data)
    console.log(localStorage.getItem('token'))
    const config = {
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Headers': 'Authorization',
        "x-access-token": localStorage.getItem('token'),
        'Authorization': 'Bearer ' + localStorage.getItem('token'),
      },
    };

    axios.post(urlbackend + "/pickup/group/add", { "data": data }, config)
      .then(response => {
        console.log(response.data)
        //        window.location.href= "/datta-able/react/default/v3/ListPackage"

        alert("suucceful")
        setData([])
      })
      .catch(e => {
        console.log(e)
      })

  }
  const saveFile = () => {

      axios.get(urlbackend + "/donwloadfile", { responseType: 'arraybuffer' })
      .then(response => {
        const blob = new Blob([response.data], {
          type:
            'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet',
        });

        fileDownload(blob, `formulaire.xlsx`); // npm " js-file-download" responsible for downloading the file for the user
      });





  };


  return (

    <div style={{ background: "#FFFFFF", height: "900px" }} className="table-responsive">
      {/* {data.length} */}
      {/* {JSON.stringify(data)} */}
      <Button onClick={() => saveFile()} style={{ "float": "right" }}  > <span className="fa fa-print"></span>{i18n.t('download-excel-file')}</Button>
      <h5 style={{ "float": "right", color: "red", marginTop: "10px", marginRight: "20px" }}>{i18n.t('use-platform-excel-file-mandatory')}</h5>
      {/* <input
        type="file"
        accept=".csv,.xlsx,.xls"
        onChange={(e) => handleFileUpload(e)}
      /> */}
      <input type="file" accept=".csv,.xlsx,.xls" onChange={(e) => { handleFileUpload(e); e.target.value = null }} />
      <Button hidden={!isEmpty} onClick={() => senddata()} > Envoyer </Button>
      {/* <DataTable
        pagination
        highlightOnHover
        columns={columns}
        data={data}
      /> */}
      <div className="app-row">
        <div className="app-col-xs-12 app-col-md-12">
          <BootstrapTable
            remote={{ cellEdit: true }}
            keyField="num"
            data={data}
            columns={columnsss}
            onTableChange={handleTableChange}
            cellEdit={cellEditFactory({
              mode: 'click',
              blurToSave: true
            })}

          />

        </div>
      </div>
    </div>

  );
}

export default AddPickups;